export default {
	data() {
		return {
			beginClientX: 0 /*距离屏幕左端距离*/,
			mouseMoveStata: false /*触发拖动状态  判断*/,
			maxwidth: '100px' /*拖动最大宽度，依据滑块宽度算出来的*/,
			confirmWords: '拖动滑块验证' /*滑块文字*/,
			confirmSuccess: false /*验证成功判断*/
		};
	},
	onReady() {
		(async () => {
			let width1 = 0;
			let width2 = 0;
			let view1 = await uni.createSelectorQuery().select('.drag');
			// console.log('view1'+JSON.stringify(document.getElementsByClassName('move').))
			view1Z
				.fields(
					{
						size: true,
						scrollOffset: true
					},
					data => {
						console.log('得到节点信息' + JSON.stringify(data));
						console.log('节点的宽为' + data.width);
						width1 = data.width;
					}
				)
				.exec();

			// let view2 = await uni.createSelectorQuery().select('.move');
			// console.log('view')
			// view2
			// 	.fields(
			// 		{
			// 			size: true,
			// 			scrollOffset: true
			// 		},
			// 		data => {
			// 			console.log('得到节点信息' + JSON.stringify(data));
			// 			console.log('节点的宽为' + data.width);
			// 			width2 = data.width;
			// 		}
			// 	)
			// 	.exec();
			this.maxwidth = width1 - this.$refs.moveDiv.clientWidth;
			document.getElementsByTagName('html')[0].addEventListener('touchmove', this.mouseMoveFn);
			document.getElementsByTagName('html')[0].addEventListener('touchend', this.moseUpFn);
		})();
	},
	methods: {
		mousedownFn: function(e) {
			console.log('eeeeeeee' + JSON.stringify(e));
			if (!this.confirmSuccess) {
				e.preventDefault && e.preventDefault(); //阻止文字选中等 浏览器默认事件
				this.mouseMoveStata = true;
				this.beginClientX = e.changedTouches[0].clientX;
			}
		}, //mousedoen 事件
		successFunction() {
			this.confirmSuccess = true;
			this.confirmWords = '验证通过';
			if (window.addEventListener) {
				document.getElementsByTagName('html')[0].removeEventListener('touchmove', this.mouseMoveFn);
				document.getElementsByTagName('html')[0].removeEventListener('touchend', this.moseUpFn);
			} else {
				document.getElementsByTagName('html')[0].removeEventListener('touchend', () => {});
			}
			document.getElementsByClassName('drag_text')[0].style.color = '#fff';
			document.getElementsByClassName('handler')[0].style.left = this.maxwidth + 'px';
			document.getElementsByClassName('drag_bg')[0].style.width = this.maxwidth + 'px';
		}, //验证成功函数
		mouseMoveFn(e) {
			if (this.mouseMoveStata) {
				let width = e.changedTouches[0].clientX - this.beginClientX;
				if (width > 0 && width <= this.maxwidth) {
					document.getElementsByClassName('handler')[0].style.left = width + 'px';
					document.getElementsByClassName('drag_bg')[0].style.width = width + 'px';
				} else if (width > this.maxwidth) {
					this.successFunction();
				}
			}
		}, //mousemove事件
		moseUpFn(e) {
			this.mouseMoveStata = false;
			var width = e.changedTouches[0].clientX - this.beginClientX;
			if (width < this.maxwidth) {
				document.getElementsByClassName('handler')[0].style.left = 0 + 'px';
				document.getElementsByClassName('drag_bg')[0].style.width = 0 + 'px';
			}
		} //mouseup事件
	}
};
